import { Component, Input, OnInit } from '@angular/core'
import { MeasurementMeanData } from 'local_modules/rppg/dist/lib/RPPGSocket.types'
import { translations } from 'src/consts/translations'
import { SCHEMA } from 'src/consts/'
@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.scss']
})

export class InfoComponent implements OnInit {
  @Input() processing!: boolean
  @Input() bgrData!: MeasurementMeanData
  @Input() lang!: string

  constructor() { }

  ngOnInit() {}

  get description () {
    return translations[this.lang]['description'] || ''
  }

  get schema() {
    return SCHEMA.map(item => ({
      ...item,
      name: translations[this.lang][item.key]
    }))
  }
}
